:root {
  --dark-color: #212529;
  --darkgray-color:#555;
  --dark-transparent-color: rgba(33,37,41, 0.25);
  --gray-color: #999999;
  --icon-color:#ffffff;
  --lightgray-color: #dddddd;
  --lightgray-transparent-color: rgba(221,221,221, 0.25);
  --main-color: #5285c5;
  --main-transparent-color: rgba(82, 133, 197, 0.25);
  --red-color: #ff0000;
  --white-color: #ffffff;
}

.App { font: 3vmin 'CenturyGothic', 'Century Gothic'; scroll-behavior: smooth;}
@media (max-width: 576px) { .App { font: 4.5vmin 'CenturyGothic', 'Century Gothic'; }}
@media (max-width: 768px) { .App { font: 4vmin 'CenturyGothic', 'Century Gothic'; }}
@font-face {
  font-family: "CenturyGothic"; font-style: normal; font-weight: normal;
  src: url("../fonts/CenturyGothic.ttf") format("truetype");
}
.century-gothic { font-family: 'CenturyGothic', 'Century Gothic' !important;}

.fs-sm-1 {}
.fs-sm-2 {}
@media (max-width: 576px) {
  .fs-sm-1 { font-size: 0.6rem !important;}
  .fs-sm-2 { font-size: 0.8rem !important;}
  .fs-sm-3 { font-size: 1.0rem !important;}
}

.gray-color { color: var(--gray-color) !important; }
.main-color { color: var(--main-color) !important; }
.lightgray-color { color: var(--lightgray-color) !important; }
.bg-main-color { background-color: var(--main-color) !important; }
.bg-dark-color { background-color: var(--dark-color) !important; }
.dark-color-hover:hover { color: var(--dark-color) !important; }
.gray-color-hover:hover { color: var(--gray-color) !important; }
.main-color-hover:hover { color: var(--main-color) !important; }
.white-color { color: var(--white-color) !important }

hr { opacity: 0.5; }
.smooth { transition: all 0.25s ease; }
.mt-logo { margin-top: 0.08em !important; }
.lh-0 { line-height: 0.5; }
.z-10000 { z-index: 10000 !important;}
.w-responsive { width: 94% !important; }
@media (max-width: 576px) { .w-responsive { width: 87.5% !important; } }

.justify-items-center { justify-items: center; }

/*************************** Men√∫ **************************/
.offcanvas { width: 4em !important; }
.offcanvas-header { padding-bottom: 0 !important;  }
.offcanvas-body { padding-top: 0 !important; }

/*************************** HEADER **************************/

.backgroundImage { background: url("../images/background.svg") repeat; }

/*************************** BODY **************************/
.table {
  border-collapse: collapse;
  border-color: var(--lightgray-color);
  width: 100%;
}
.table th  {
  background-color:var(--main-color) !important;
  color: var(--white-color);
  font-weight: normal;
}
.table th tr  {
  background-color:var(--main-color) !important;
}
.table td, .table {
  background-color: transparent !important;
  color: var(--dark-color);
}
.table tr:nth-child(even) { background-color:var(--white-color); }
.table tr:nth-child(odd) { background-color: #f7f7f7; }
.table tr:nth-child(even):hover { background-color:var(--main-transparent-color); }
.table tr:nth-child(odd):hover { background-color:var(--main-transparent-color); }

/*************************** Sort **************************/
.active { color: var(--main-color) !important;}              /* Buttons */
.hover:hover { background-color: #f3f3f3 !important; }

.button {
  font-family: 'Century Gothic','centuryGothic'!important;
  background-color: var(--main-color)!important;
  color: var(--icon-color);
  width: 100%;
  text-align: center;
  transition: all 0.25s ease;
}

.button:hover {
  box-shadow: 0 10px 20px -10px rgba(100, 100, 100, 0.8)!important;
  transform: translateY(-5px);
}

input {
  font-family: 'Century Gothic', 'centuryGothic'!important;
  color: var(--darkgray-color);
  width: 100%;
  height: 3vmin !important;
  background-color: var(--lightgray-color);
  border: none;
}
label {
  font-size: 1rem;
  font-family: 'Century Gothic', 'centuryGothic'!important;
  color: var(--darkgray-color)!important;
  min-width: 10vmin!important;
  text-align: left;
}
.idText {
  text-align: left;
  margin-left: -1vmin !important;
}

input[type='number'] {
  color: var(--darkgray-color)!important;
}
input[type='number']::-webkit-inner-spin-button,
input[type='number']::-webkit-outer-spin-button {
  background-color: var(--white-color);
  font-size: inherit;
  opacity: 0;
  height: 6vmin;
  padding: 0 12px 0 12px;
  cursor: pointer;

}
input[type='date'] {
  color: var(--darkgray-color)!important;
  width: 100%;
  cursor: pointer;
}
input[type='time'] {
  color: var(--darkgray-color)!important;
  width: 100%;
  cursor: pointer;
}
input[type="date"]::-webkit-calendar-picker-indicator ,
input[type="time"]::-webkit-calendar-picker-indicator {
  color: var(--main-color);
  filter: invert(0.5);
  color-scheme: inherit;
  cursor: pointer;
}
select {                                          /* Select views */      
  background-color: var(--white-color);
  font-size: inherit;
  font-family: inherit;
  color: var(--darkgray-color);
  width: 100%;
  height: 100%;
  border: none;
  padding-inline-start: 1vmin;
  padding: 0.5vmin 0;
  cursor: pointer;
}
select::-ms-expand { display: none; }

.dateSelector {
  padding: 0 0 0 0.75vmin !important;
  border:none;
  box-shadow: none;
}
.timeSelector {
  padding: 0 0 0 0.75vmin !important;
  border:none;
  box-shadow: none;
}


/*************************** Modal Alerts **************************/
.modalTable {
  border-collapse: collapse;
  border-color: var(--lightgray-color);
}

/**************************** Loader SUSPENSE ************************/
/********** Loader Balls **********/
.loaderBalls {                                          /* Loader Balls */   
  display: grid;
  place-content: center center;
  height: 100vh;
}
.loading{
  width: 60px;
  height: 30px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto; 
}
.loadingTitle{ padding-top:20px }
.balls {
  --balls-size: 14px;
  width: var(--balls-size);
  height: var(--balls-size);
  background-color: var(--main-color);
  box-shadow: 0 10px 20px -10px rgba(100, 100, 100, 0.8)!important;
  animation: jump .5s alternate infinite;
  border-radius: 50%  
}
.balls:nth-child(2) { animation-delay: .18s; }
.balls:nth-child(3) { animation-delay: .37s; }
@keyframes jump { 
    from { transform: scaleX(1.25); } 
    to{ transform: translateY(-50px) scaleX(1); }
}

/********** Loader Clock **********/
.loaderClock {                                          /* Loader Clock */ 
  --clock-color: var(--main-color);
  --clock-width: 2.5rem;
  --clock-radius: calc(var(--clock-width) / 2);
  --clock-minute-length: calc(var(--clock-width) * 0.4);
  --clock-hour-length: calc(var(--clock-width) * 0.2);
  --clock-thickness: 0.15rem;
  position: relative;
  margin-top: 2em;
  display: flex;
  justify-content: center;
  align-items: center;
  width: var(--clock-width);
  height: var(--clock-width);
  border: 3px solid var(--clock-color);
  border-radius: 50%;

  &::before,
  &::after {
    position: absolute;
    content: "";
    top: calc(var(--clock-radius) * 0.25);
    width: var(--clock-thickness);
    background: var(--clock-color);
    border-radius: 10px;
    transform-origin: center calc(100% - calc(var(--clock-thickness) / 2));
    animation: spin infinite linear;
  }

  &::before {
    height: var(--clock-minute-length);
    animation-duration: 2s;
  }

  &::after {
    top: calc(var(--clock-radius) * 0.25 + var(--clock-hour-length));
    height: var(--clock-hour-length);
    animation-duration: 15s;
  }
}



